<!-- 兑奖成功 -->
<template>
	<xh-dialog ref="xhDialog" @closeNotice="confirm">
		<view class="confirm-exchange">
			   <!-- title -->
			   <image class="ce-success" src="../static/success.png"/>
			   <!-- msg -->
			   <view class="ce-msg">恭喜兑奖成功！</view>
			   <!-- 马上兑奖 -->
			   <view class="xh-toast-button change-now" @click="confirm">确定</view>
		</view>
	</xh-dialog>
</template>

<script>
		export default{
			methods:{
				show(){
					this.$refs.xhDialog.show()
				},
				close(){
